<template>
	<div id="app">
		<mt-header title="内涵段子"></mt-header>
		<div v-infinite-scroll="loadMore" infinite-scroll-disabled="loading" infinite-scroll-distance="50">
			<textbox v-if="textlsit" :app-list="textlsit"></textbox>
		</div>
	</div>
</template>

<script>
	import textbox from './textbox.vue'
	import { Indicator } from 'mint-ui';
	export default {
		name: 'app',
		data() {
			return {
				textlsit: [],
				loading:false
			}
		},
		components: {
			textbox
		},
		methods: {
			loadMore() {
				
					this.listAjax()
					
			
			},
			listAjax(){
				Indicator.open('加载中...');
				this.$http.get('https://www.apiopen.top/satinGodApi?type=1&page=1', ).then((data) => {
				console.log(data)
				for(var i=0;i<data.data.data.length;i++){
					this.textlsit.push(data.data.data[i])
				}
				Indicator.close();
			})
			}
		},
		mounted() {
			this.listAjax()
		}
	}
</script>

<style>
	* {
		margin: 0;
		padding: 0;
	}
</style>


