<template>
<div>
	<div style="margin: 10px 10px;border-bottom: 2px solid #CCCCCC;" v-for="i in appList">
		<!--头部描述-->
	     <div style="line-height: 80px; overflow: hidden;">
	      <img :src="i.header" style="height: 80px;float: left;border-radius: 5px;"> 
	      <div style="float: left;margin-left: 10px;">
	      	      {{i.username}}
	      </div>
	     </div> 
	     <!--描述-->
	     <div style="line-height: 50px;">
	     	{{i.text}}
	     </div>
	     <!--内容-->
	     <div style="min-height: 200px;background: #CCCCCC;" v-if="i.type!='text'">
	     	<div v-if="i.type == 'video'">
	     		<video width="100%" controls :src="i.video"></video>
	     	</div>
	     	<div v-if="i.type == 'image'">
	     	   <img :src="i.image" width="100%" />
	     	</div>
	     	
	     	<div v-if="i.type == 'gif'">
	     		<img :src="i.gif" width="100%" />
	     	</div>
	     </div>
	     <!--底部-->
	    <div id="textbox_floot">
	    	<span>点赞 {{i.up}}</span>
	    	<span>踩 {{i.down}}</span>
	    	<span>评论 {{i.forward}}</span>
	    	<span>转发</span>
	    </div>
	</div>
</div>
</template>

<script>
	export default{
		props:['appList']
	}
</script>

<style>
	#textbox_floot{
		overflow: hidden;
		line-height: 40px;
	}
	#textbox_floot span{
		float: left;
		padding: 0 15px;
	}
	#textbox_floot span:nth-last-of-type(1){
		float:right;
	}
</style>


